import React from "react";
import {
  Grid,
  Box,
  Card,
  CardHeader,
  CardContent,
  CardMedia,
  makeStyles,
  createStyles,
  Typography,
} from "@material-ui/core";
import lanbrosTextLogotype from "../assets/lanbros_logo.png";
import minecraftHeader from "../assets/minecraft.jpg";
import valheimHeader from "../assets/valheim.jpg";
import { useHistory } from "react-router-dom";

const useStyles = makeStyles(() =>
  createStyles({
    cardImage: {
      height: "200px",
    },
    cardTransform: {
      transition: "all 0.2s",
      "&:hover": {
        transform: "scale(1.05)",
      },
    },
  })
);

interface AboutProps {
  isWide: boolean;
  isUltraWide: boolean;
}

function About(props: AboutProps) {
  const classes = useStyles();
  const history = useHistory();
  const routeToHome = () => history.push("/");

  return (
    <React.Fragment>
      <Box pt={2} pb={2} pl={10} style={{ backgroundColor: "white" }}>
        <Grid container>
          <Grid item xs={4}>
            <img
              onClick={routeToHome}
              height="auto"
              width="100%"
              style={{
                maxWidth: "200px",
                cursor: "pointer",
                mixBlendMode: "difference",
                display: "flex",
              }}
              src={lanbrosTextLogotype}
            />
          </Grid>
        </Grid>
      </Box>
      <Box
        mt={5}
        ml={props.isUltraWide ? 60 : props.isWide ? 40 : 5}
        mr={props.isUltraWide ? 60 : props.isWide ? 40 : 5}
      >
        <Box mt={props.isWide || props.isUltraWide ? 20 : 5}></Box>
        <Grid container spacing={4}>
          <Grid item xs={12}>
            <Card>
              <CardHeader title="Historielektion"></CardHeader>
              <CardContent>
                <Typography variant="body1">
                  LANBROS grundades våren 2013 efter ett helglan på
                  Svarttjärnsvägen i Hudiksvall. Fem eldsjälar ställde upp som
                  de första styrelsemedlemmarna och på den vägen har det
                  fortsatt. Vi har under vår tid arrangerat brädspelskvällar,
                  paintball ute på en ö, turneringar och såklart vanliga
                  spelkvällar "
                  <a href="https://www.youtube.com/watch?v=CewJ-ihIqaM">
                    on-the-line
                  </a>
                  ".
                </Typography>
                <Typography variant="body1">test</Typography>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Box>
    </React.Fragment>
  );
}

export default About;
